#startLoad{
  position: fixed;
  display:none;
  align-items: center;
  justify-content: center;
  top:0px;
  bottom:0px;
  height:110vh;
  left:0px;
  right:0px;
  z-index:200;
  background-color: rgb(193, 207, 241);
  transition: all 1s ease-in-out 0s;
}
#startLoad img{
  width:20%;
  border-radius: 100%;
  animation : logo 2.5s linear 0s infinite forwards normal;
  transition: all 0.5s;
}
@keyframes logo{
  0%{
    transform: scale(1);
  }
  50%{
    transform: scale(1.2);
  }
  100%{
    transform: scale(1);
  }
}

#loader{
  position: fixed;
  display:none;
  align-items: center;
  justify-content: center;
  top:0px;
  bottom:0px;
  height:110vh;
  left:0px;
  right:0px;
  z-index:100;
  background-color: rgba(193, 207, 241, 0.593);
  transition: all 1s ease-in-out 0s;
}
#ring{
  width:100px;
  height:100px;
  border-radius: 50%;
  animation: ring 1s linear 0s infinite forwards normal;
  transition: all 0.5s;
}
@keyframes ring {
  0%{
      transform: rotate(0deg);
      box-shadow: 1px 5px 2px rgb(0, 19, 58);
  }
  25%{
      transform: rotate(90deg);
      box-shadow: 1px 5px 2px rgb(36, 0, 113);
  }
  50%{
      transform: rotate(180deg);
      box-shadow: 1px 5px 2px rgb(0, 85, 255);
  }
  75%{
      transform: rotate(270deg);
      box-shadow: 1px 5px 2px rgb(36, 0, 113);
  }
  100%{
      transform: rotate(360deg);
      box-shadow: 1px 5px 2px rgb(0, 19, 58);
  }
}
#navbar{
  z-index: 100;
  position:sticky;
  top:0px;
  display: flex;
  width:100vw;
  height:8vw;
  background-color: aliceblue;
  /* #302e45; */
  justify-content: space-between;
}
#navbar img{
  margin-left: 2%;
  width:10%;
  border-radius: 100px;
}
#navbar span{
  width:50%;
  height:40%;
  margin:auto;
}
#navbar span button{
  width:15%;
  height:100%;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  background-color: #4e4b7076;
}
#search{
  width:85%;
  height:100%;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
}
#droplist{
  z-index: 2;
  margin:2px;
  color:white;
  background-color: #302e45;
}
#droplist li{
  margin:0px 6px;
}
#droplist li:hover{
  background-color: #000000;
  cursor: pointer;
}
.dropdown button{
  margin:20px;
  color:white;
  background-color: #302e45;
}

#sidebar{
  position:fixed ;
  height:100%;
  width:12%;
  padding:2% 0px;
  /* border:1px solid black; */
  background-color: aliceblue;
}
#sidebar ul{
  width:100%;
  padding:0px;
}
#upper a{
  text-decoration: none;
  color:black;
}
#sidebar li{
  padding:0;
  list-style: none;
  text-align: center;
  color:black;
}
#sidebar button{
  padding:0.9vw;
  border:0px;
  background-color: aliceblue;
  width:100%;
  height:100%;
}

#sidebar button:hover{
  background-color: rgb(199, 229, 255);
  cursor: pointer;
}
#mainPage{
  position:relative;
  /* z-index: -2; */
  min-height: 100vh;
  margin-left: 12%;
  width:88%;
  padding:2vw 5vw;
  background-color: rgb(210, 225, 234);
}
#notFound{
  display:none;
  flex-direction: column;
  align-items: center;
  z-index:3;
  position:absolute;
  top:0px;
  min-height: 100vh;
  width:92%;
  padding:10% 8% 0px 0px;
  background-color: rgb(210, 225, 234);
}
#notFound img{
  width:10%;
}
#prefer{
  z-index:4;
  position:absolute;
  top:10px;
  right:10px;
}
#prefer ul{
  width:22vw;
  padding:0px;
  color:white;
  background-color: rgb(29, 100, 162);
}
.preferInner{
  display:inline-block;
  width:50%;
  margin:0px;
  padding:1vw;
  border-left:1px solid rgb(128, 128, 128);
}
.preferInner li{
  margin:8px;
}
.preferInner h6{
  text-align: center;
  font-family: 'Times New Roman', Times, serif;
  color:rgb(219, 206, 255);
}
#ghost{
  visibility: hidden;
}
#card0{
  display:none;
}
.card{
  /* z-index: -1; */
  margin:20px 0px;
  display:flex;
  flex-direction: row;
  height:40vh;
  opacity: 0.9;
}
.card div{  
  display: inline-block;
  border: 1px solid black;
  width:30%;
}
.card img{
  width:100%;
  height:100%;
  /* height: 100%; */
  /* aspect-ratio: 3/2; */
  object-fit:cover;
}
.card span{
  display:flex;
  flex-direction: column;
  padding:0vw 2vw;
  width:70%;

}
.card span a{
  color:black;
  font-weight: bolder;
}
.card span h5{
  height:30%;
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  margin-top: 0.1vw;
}
.card span h6{
  height:20%;
  color:grey;
  margin-top:0.1vw;
  font-family:Arial, Helvetica, sans-serif;
  font-size: 0.9vw;
}
.card span p{
  display:inline-block;
  margin-top:1vw;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  color:grey;
}
@media only screen and (max-width:926px){
  #navbar{
    height:10vw;
  }
  #navbar span{
    height:60%;
    width:60%;
    margin-right:20%;
    font-size:1.5vw;
  }
  .dropdown button{
    position:absolute;
    top:10px;
    right:0px;
    margin:0px;
    font-size:1.5vw;
  }
  #sidebar{
    font-size:2.5vw;
    width:20%;
  }
  #mainPage{
    width:80%;
    margin-left:20%;
  }
  #notFound{
    width:100%;
  }
  #notFound img{
    width:50%;
  }
  #notFound p{
    font-size:2vw;
  }
  #heading{
    font-size:5vw;
  }
  .card{
    height:32vw;
  }
  .card span h3{
    font-size:2.5vw;
  }
  .card span h6{
    font-size:1.5vw;
  }
  .card span h5{
    font-size:1.8vw;
  }
  .card p{
    font-size:1.2vw;
  }
}